.footer {
    display:flex;
    position: absolute;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    border-top: 1px solid #353746;
    width: 100%;
    height: 25px;
    min-height: 10px;
    background-color: #181A20;
}

@media (max-aspect-ratio: 9/12) {
    .footer {
        display: none;
    }
}