@font-face {
  font-family: "Monaco";
  src: local("Monaco"), 
  url("./styles/fonts/Monaco.ttf") format("truetype");
}

html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.app {
  display: flex;
  height: 100vh;
  width: 100vw;
}

.content_container {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  min-height: calc(100%-10px);
}

/* Mobile Layout */
@media (max-aspect-ratio: 9/12) {
  .app {
    flex-direction: column-reverse;
  }

  .content_container {
    display: flex;
    flex-direction: column;
    min-width: 100%;
    min-height: calc(100% - 60px);
  }
}