.header {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    min-height: 30px;
    background-color: #181A20;
    box-sizing: border-box;
    border-bottom: 1px solid #353746;
}

.active_nav{
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: fit-content;
    height: 100%;
    padding: 10px 15px;
    color: #636C97;
    border-bottom: #FD7BC7 1px solid;
    border-right: #181A20 1px solid;
    background-color: #282B36;
    font-size: 16px;
    font-style: italic;
}

.inactive_nav{
    display: flex;
    align-items: center;
    width: fit-content;
    height: 100%;
    padding: 10px 20px;
    box-sizing: border-box;
    background-color: #20222C;
    color: #636C97;
    border-right: #181A20 1px solid;
    font-size: 16px;
    padding-right: 30px;
}

.inactive_nav:hover{
    background-color: #282B36;
}

.icon {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 5px;
}

/* Mobile Layout */
@media (max-aspect-ratio: 9/12) {
    .header {
        height: 80px;
    }

    .mobile_header {
        display: flex;
        align-items: center;
        color: white;
        width: 100%;
    }

    .header_icon {
        aspect-ratio: 1/1;
        height: 100%;
        border-right: 2px solid green;
    }

    .header_title {
        width: calc(100% - 120px);
        padding-left: 20px;
        font-size: 20px;
        font-style: bold;
    }

    .menu_button {
        aspect-ratio: 1/1;
        height: 100%;
        border-left: 2px solid cyan;
    }
}